/* eslint-disable */

import { createContext, useState } from 'react';
import { Navbar, Container, Nav, Row, Col } from 'react-bootstrap';
import './App.css';
import bg from './img/bg.png';
import data from './data.js';
import { Routes, Route, Link, useNavigate, Outlet, useParams } from 'react-router-dom';
import axios from 'axios';

import Detail from './pages/Detail';
import Cart from './pages/Cart';

export let Context1 = createContext()

function App(){

  let [shoes, setShoes] = useState(data)
  let [inventory] = useState([10, 11, 12])
  let [showLoding, setLoding] = useState(false)
  let navigate = useNavigate();
  let [sortText, setText] = useState('A-Z')
  let [count, setCount] = useState(2)

  return (
    <div className="App">

      
      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand href="#home">G-Shop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{ navigate('/') }}>Home</Nav.Link>
            {/* <Nav.Link onClick={()=>{ navigate('/detail/0') }}>Detail</Nav.Link> */}
            <Nav.Link onClick={()=>{ navigate('/about') }}>About</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/event') }}>Event</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/cart') }}>Cart</Nav.Link>
          </Nav>
        </Container>
      </Navbar>
      

      <Routes>
        <Route path="/" element={
          <>
          <div className="main-bg" style={{ backgroundImage : 'url('+ bg +')' }}></div>
          <button onClick={()=>{
            let abcSort = [...shoes]

            if(sortText == 'A-Z'){
              setText('Z-A')
              console.log(sortText)
              abcSort = abcSort.sort(function(a,b){
                if( a.title < b.title ) {
                  return -1
                } else {
                  return 1
                }
              })
            }
            if(sortText == 'Z-A') {
              setText('A-Z')
              console.log(sortText)
              abcSort = abcSort.sort(function(a,b){
                if( a.title < b.title ) {
                  return 1
                } else {
                  return -1
                }
              })
            }

            
            console.log(abcSort)
            setShoes(abcSort);

            

          }}>{sortText}</button>
          <Container>
            <Row>
              {
                shoes.map((a, i)=>{
                  return (
                    <InfoPage shoes={shoes[i]} key={i} navigate={navigate} />
                  )
                })
              }
            </Row>
          </Container>
          {
            showLoding == true ? <Loding /> : null
          }
          <button onClick={()=>{
            setLoding(true)
            axios.get(`https://codingapple1.github.io/shop/data${count}.json`)
            .then((result)=>{
              let addArr = [...shoes]
              addArr = [...shoes, ...result.data]
              setShoes(addArr)
              setTimeout(function(){setLoding(false)}, 300)
            })
            .catch(()=>{
              setLoding(false)
              console.log('실패함 수거')
              // setLoding(false)
            })
            setCount(count+1)
            if(count >= 4) {
              setLoding(false)
              setTimeout(function(){alert('더 이상 상품 없음')}, 500)
            }
          }}>버튼</button>
          </>
        }/>
        <Route path="/detail/:id" element={
          <Context1.Provider value={{ inventory }}>
            <Detail shoes={shoes}/>
          </Context1.Provider>
        }/>
        <Route path="/about" element={<About />}>
          <Route path="member" element={<div>멤버임</div>}/>
          <Route path="location" element={<div>위치정보임</div>}/>
        </Route>

        <Route path="/Event" element={<Event />}>
          <Route path="one" element={<div>첫 주문시 양배추즙 서비스</div>}/>
          <Route path="two" element={<div>생일기념 쿠폰받기</div>}/>
        </Route>

        <Route path="/cart" element={<Cart />} />

      </Routes>

      


    </div>
  );
}
function InfoPage(props){
  return (
    <Col md={4} onClick={()=>{ props.navigate(`/detail/${props.shoes.id}`);}}>
      <img src={`https://codingapple1.github.io/shop/shoes${props.shoes.id+1}.jpg`} width="80%"/>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.price}</p>
    </Col>
  )
}
function About(){
  return (
    <div>
      <h4>회사정보임</h4>
      <Outlet></Outlet>
    </div>
  )
}
function Event(){
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  )
}
function Loding(){
  return (
    <div style={{position:'fixed', top:'50%', left:'50%', fontSize:'72px', transform:'translateX(-50%)'}}>
      <h1>로딩중</h1>
    </div>
  )
}
export default App;
