import {Table} from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { changeName, increase } from '../store/userSlice';

// store 변경 함수
import { upCount, downCount, delItem } from '../store/cartSlice'

function Cart(){

  let dispatch = useDispatch()
  let store = useSelector((state)=>state)
  let cart = store.cart

  return (
    <div>

    <h6>{store.user.name} {store.user.age}의 장바구니</h6>
    <button onClick={()=>{ dispatch(increase(100)); }}>버튼</button>

      <Table>
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경하기</th>
            <th>선택삭제</th>
          </tr>
        </thead>
        <tbody>
          {
            store.cart.map((a, i)=>
              <tr key={i}>
                <td>{cart[i].id}</td>
                <td>{cart[i].name}</td>
                <td>{cart[i].count}</td>
                <td>
                  <button onClick={()=>{ dispatch(upCount(cart[i].id)); }}>+</button>
                  <button onClick={()=>{ dispatch(downCount(cart[i].id)); }}>-</button>
                </td>
                <td>
                  <button onClick={()=>{ dispatch(delItem(cart[i].id))}}> X </button>
                </td>
              </tr>
            )
          }
        </tbody>
      </Table>
    </div>
  )
}
export default Cart;