/* eslint-disable */

import { Nav } from "react-bootstrap";
import { useParams } from "react-router-dom";
import { useEffect } from "react";
import { useState } from "react";
import styled from 'styled-components';
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

// store 변경 함수
import { addItem } from '../store/cartSlice';

let YellowBtn = styled.button`
  background : ${ props => props.bg };
  color: ${ props => props.bg == 'blue' ? 'white' : 'black'};
  padding: 10px;
`
let Box = styled.div`
  background : grey;
  padding : 20px;
`
let Input = styled.input`
  width : 200px;
`

function Detail(props){
  
  let navigate = useNavigate()
  let dispatch = useDispatch();
  let store = useSelector((state)=>state);

  let {id} = useParams();
  let searchProduct = props.shoes.find(function(e){ return e.id == id })
  
  let [count, setCount] = useState(0);
  let [aalert, setAlert] = useState(true);
  let [num, setNum] = useState('');
  let [tabs, setTabs] = useState(0);
  let [start, setStart] = useState('')

  useEffect(()=>{
    let a = setTimeout(()=>{
      setStart('end')
    }, 10)
    console.log('마운트')
    return ()=>{
      clearTimeout(a)
      setStart('')
      console.log('언마운트')
    }
  }, [])

  useEffect(()=>{
    {
      isNaN(num) == true
      ? alert('한글 노노')
      : null
    }
  }, [num])

  return (
    <div className={`container start ${start}`}>
      {
        aalert == true  
        ? 
          <div className="alert alert-warning">
            10초이내 구매시 할인
          </div>
        : null
      }

      {count}
      <button onClick={()=>{ setCount(count+1) }}>버튼</button>
      <Box>
        <YellowBtn bg="blue">버튼</YellowBtn>
        <YellowBtn bg="orange">버튼</YellowBtn>
      </Box>
      <div className="row">
        <div className="col-md-6">
          <img src={`https://codingapple1.github.io/shop/shoes${searchProduct.id+1}.jpg`} width="100%" />
        </div>


        <div className="col-md-6">
          <h4 className="pt-5">{searchProduct.title}</h4>
          <p>{searchProduct.content}</p>
          <p>{searchProduct.price}</p>
          <button className="btn btn-danger" onClick={()=>{
            dispatch(addItem({id : searchProduct.id, name : searchProduct.title, count: 1},
            navigate('/cart')
          ))}}>주문하기</button>
          <br />
          <br />
          <Input onChange={(e)=>{ setNum(e.target.value) }}/>
        </div>
      </div>

      <Nav variant="tabs" defaultActiveKey="link0">
        <Nav.Item>
          <Nav.Link onClick={()=>{setTabs(0)}} eventKey="link0">버튼0</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link onClick={()=>{setTabs(1)}} eventKey="link1">버튼1</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link onClick={()=>{setTabs(2)}} eventKey="link2">버튼2</Nav.Link>
        </Nav.Item>
      </Nav>
      <TabContent tabs={tabs} />
    </div>
  )
}

function TabContent({tabs}){

  let [fade, setFade] = useState('')

  useEffect(()=>{
    let a = setTimeout(()=>{setFade('end')}, 100)
    return()=>{
      clearTimeout(a)
      setFade('')
    }
  }, [tabs])

  return (<div className={`start ${fade}`}>
    { [<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][tabs] }
  </div>)
}
export default Detail;