/* eslint-disable */

import { createSlice } from '@reduxjs/toolkit'

let cart = createSlice({
  name : 'cart',
  initialState : 
  [
    {id : 8, name : 'White and Black', count : 1},
    {id : 9, name : 'Grey Yordan', count : 1},
    {id : 10, name : '141414', count : 1},
    {id : 11, name : '232323', count : 1}
  ],
  reducers : {
    // 상품 추가
    addItem(state, action){
      {
        state.findIndex( e => e.id == action.payload ) ? state.push(action.payload) : alert('이미 있음')
      }
    },
    // 선택 삭제
    delItem(state, action){
      let num = state.findIndex((e)=>{return e.id == action.payload})
      state.splice(num, 1)
      // state.filter((e) => e.id != state[num])
    },
    // 수량 +1
    upCount(state, action){
      let num = state.findIndex( e => e.id === action.payload )
      state[num].count+=1
    },
    // 수량 -1
    downCount(state, action){
      let num = state.findIndex( e => e.id === action.payload )
      {
        state[num].count == 0 ? null : state[num].count-=1
      }
    },
  }
})

export let { addItem, delItem, upCount, downCount } = cart.actions

export default cart;